import * as React from "react";
import { Modal, ModalBody } from "reactstrap";

export const StoreSelectionModal = (props: { storeData: any[] }) => {
  const [isOpen, setModalOpen] = React.useState(false);
  React.useEffect(() => setModalOpen(props.storeData.length > 0), [
    props.storeData,
  ]);

  return (
    <Modal
      className="StoreModal"
      isOpen={isOpen}
      toggle={() => setModalOpen(false)}
    >
      <ModalBody>
        <div className="HeadText">Hurray! We're now open.</div>
        {props.storeData.map((store: any) => {
          return (
            <div className="StoreDetails">
              <div className="Manager">{store.storeManager}</div>
              <div className="Contact">
                {" "}
                <span>Contact No. </span>
                {store.phoneNo}
              </div>
              <h4>STORE NAME</h4>
              <div className="StoreName">{store.storeName}</div>
              <h4>OPEN TIMIINGS</h4>
              <div className="Timing">
                <span>Customer Entry on</span> {store.operationalDays} -{" "}
                {store.operationalTime}
              </div>
            </div>
          );
        })}
      </ModalBody>
    </Modal>
  );
};
